import React, { useState, useEffect } from 'react'

import {
  Collapse,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Button
} from 'reactstrap'
import PaymentMethodsTest from './PaymentMethodsTest'
const Payments = (props) => {
  const [plan, setplan] = useState([])
  const temp = [
    {
      id: 0,
      title: 'Credit / Debit / ATM Card',
      content: 'Credit / Debit / ATM Card'
    },
    {
      id: 1,
      title: 'Net Banking',
      content: 'Net Banking'
    },
    {
      id: 2,
      title: 'Wallet',
      content: 'Wallet'
    },
    {
      id: 3,
      title: 'UPI',
      content: 'UPI'
    }
  ]
  return (
    <>
      <div className="list-view product-checkout" style={{ display: 'flex' }}>
        <div style={{ width: '80%' }}>
          <PaymentMethodsTest heading="Payment Options" collapseItems={temp} />
        </div>
        <div className="amount-payable checkout-options">
          <Card>
            <CardHeader>
              <CardTitle>Price Details</CardTitle>
            </CardHeader>
            <CardBody>
              <p>Plan :{plan ? plan : 'Basic'}</p>
              <span className="text-muted mt-25">Select</span>
              <div className="detail">
                <div className="details title">Price of 3 items</div>
                <div className="detail-amt">
                  <strong>$699.30</strong>
                </div>
              </div>
              <div className="detail">
                <div className="details title">Delivery Charges</div>
                <div className="detail-amt discount-amt">
                  <strong>Free</strong>
                </div>
              </div>
              <hr />
              <div className="detail">
                <div className="details title">Amount Payable</div>
                <div className="detail-amt">
                  <strong>$699.30</strong>
                </div>
              </div>
            </CardBody>
          </Card>
        </div>
      </div>
      <div className="d-flex justify-content-end flex-wrap mt-2">
        <Button.Ripple
          className="mr-1"
          color="primary"
          onClick={(e) => {
            // this.updateuser(e)
            console.log('dzfxgchj')
          }}
        >
          Pay
        </Button.Ripple>
      </div>
    </>
  )
}

export default Payments
